<template>
  <div class="container mx-auto font-Popin px-4">
    <Header heading="Anime Quote Generator"/>
    <Quote v-if="quoting" :quoting="quoting"/>
    <Skeleton class="mt-8" v-else/>
    <Button btnText="Generate Quote" @click="getQuote"/>
    <QuoteList v-if="quotes.length" class="mb-4" :recents="quotes" />
  </div>
</template>

<script>
import Header from './components/Header.vue'
import Quote from './components/Quote.vue'
import Skeleton from './components/Skeleton.vue'
import Button from './components/Button.vue'
import QuoteList from './components/QuoteList.vue'

export default {
  name: 'App',
  components: {
    Header,
    Quote,
    Skeleton,
    Button,
    QuoteList
  },
  data() {
    return {
      quoting: null,
      quotes: []
    }
  },
  methods: {
    async getQuote() {
    if(this.quoting != null){
      this.quotes =  [...this.quotes, this.quoting]
      console.log(this.quotes)
    }
    const res = await fetch('https://animechan.vercel.app/api/random')
    const data = await res.json()
    this.quoting = data
    }
  },
  async created() {
    this.getQuote()
  }
}
</script>

